<template>
    <div class="container">
        <h1>{{ album.name }}</h1>
        <div class="text-end">
            <i>{{ moment(album.updated_at).format("YYYY-MM-DD HH:MM") }}</i>
        </div>
        <div class="alert alert-light" role="alert" v-html="album.description"></div>
        <p class="vue-html" v-html="album.context"></p>
        <div class="text-center">
            <span>瀏覽次數: {{ album.views }}</span>
        </div>
        <b-row>
            <b-col md="4 p-1" v-for="(item, index) in photos" :key="index">
                <b-img :src="'http://localhost/ntcust_lang_backend/storage/app/' + item.file.path" fluid alt="Responsive image"></b-img>
            </b-col>
        </b-row>
        <div class="text-center">
            <button type="button" class="btn btn-secondary my-3" @click="historyPrev">回上頁</button>
        </div>
    </div>
</template>

<script>
import moment from 'moment';
import { AlbumServer, FileServer } from '../../providers/http-server';

export default {
    name: 'App',
    data(){
        return {
            album: [],
            photos: []
        }
    },
    created(){
        this.moment = moment;
        AlbumServer.item(this.$route.params.id).then(
            (response) => this.album = response.data
        );
        FileServer.filter(5, this.$route.params.id).then(
            (response) => this.photos = response.data
        )
    },
    methods: {
        historyPrev() {
            this.$router.go(-1);
        }
    }
}
</script>

<style scoped>
@import '../../libs/ckeditor_plugin.css';
</style>